@use "../../scss/index" as *;

.modal {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.5);
	z-index: $top;

	display: flex;
	justify-content: center;
	align-items: center;

	.modal-content {
		@include borderRadius();
		background-color: #fff;
		width: min(340px, 90%);

		.modal-header {
			@include borderRadius();
			display: flex;
			flex-direction: row;
			background-color: $advizot-color;
			box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);

			span {
				@include advizotFont(1.25rem, 400, 0);
				margin: 0.75rem 0.9rem;
			}

			.close-x {
				cursor: pointer;
				margin-left: auto;
				padding: 0 1rem;
				background-color: transparent;
				border-color: transparent;
				font-size: 2.25rem;
				@include transition(transform);
				transition-duration: 100ms;

				&:focus {
					@include onFocus();
				}

				&:hover {
					transform: rotate(90deg);
				}
			}
		}

		.modal-body {
			display: flex;
			flex-direction: column;
			margin: 0 1rem 1rem;

			.btn {
				margin: 0.75rem auto;
				width: calc(100% - ($form-padding * 2));
			}
		}
	}
}

.signin-btn {
	margin-top: 1rem;

	button {
		@include borderRadius();
		cursor: pointer;
		display: flex;
		align-items: center;
		width: 100%;
		font-family: $main-font;
		font-size: 1.1rem;
		font-weight: 600;
		background-color: $advizot-color;
		color: black;
		border: none;
		padding: 1rem 1.25rem;
		@include transition(all);

		.logo {
			width: 30px;
			margin: 0 7px;
		}
		span {
			margin: 0 auto;
		}
	}

	.linkedin-hover {
		&:hover {
			@include loginHover(#0077b5, 30px);
		}
	}
	.google-hover {
		&:hover {
			@include loginHover(#34a853, 30px);
		}
	}
}
